<template>
  <control v-bind="props" v-slot="flags">
    <template v-if="showImageEditor">
      <image-editor
        :imageUrl="value"
        :cropperData="{}"
        @cancel="cancelImageEditor"
        @save="saveImageEditor"
      ></image-editor>
    </template>
    <div v-show="!showImageEditor">
      <input
        ref="input"
        type="file"
        name="image"
        accept="image/*"
        @change="setImage"
        class="form-control-file"
        style="margin-bottom: 10px"
      />
      <input
        type="text"
        class="form-control"
        :aria-describedby="options.label"
        v-model="model"
        :class="{ 'is-invalid': flags.invalid && flags.touched }"
        style="margin-bottom: 10px"
      />

      <div>
        <a v-if="imageSrc" href="#" @click.prevent="edit" title="Edit Image">
          <img
            :src="imageSrc"
            alt="Image"
            style="margin: 10px 0"
            class="img-fluid"
          />
        </a>
      </div>
    </div>
  </control>
</template>

<script>
//import Vue from "vue";
import ControlField from "./ControlField.vue";
import Control from "./Control.vue";
import ImageEditor from "./ImageEditor.vue";
import VueCropper from "vue-cropperjs";
import "cropperjs/dist/cropper.css";

import FileBrowser from "./FileBrowser.vue";



let ImageField = {
  name: "ImageField",
  extends: ControlField,
  props: {
    value: {
      type: String,
    },
  },
  data() {
    return {
      showImageEditor: false,
    };
  },
  computed: {
    imageSrc() {
      return this.value + new Date().getTime();
    },
  },
  methods: {
    setImage(e) {
      const file = e.target.files[0];
      if (file.type.indexOf("image/") === -1) {
        alert("Please select an image file");
        return;
      }
      let config = {
        file: file,
        name: file.name,
        folder: this.baseFolder,
      };
      this.connector.upload(
        config,
        (data) => {
          this.model = data.url;
        },
        () => {}
      );

      /*
      if (typeof FileReader === "function") {
        const reader = new FileReader();
        reader.onload = event => {
          this.model = {
            url: event.target.result,
          };
          // rebuild cropperjs with the updated source
          //this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert("Sorry, FileReader API not supported");
      }
      */
    },
    showFileChooser() {
      this.$refs.input.click();
    },
    edit() {
      this.showImageEditor = true;
    },
    cancelImageEditor() {
      this.showImageEditor = false;
    },
    saveImageEditor(canvas) {
      this.showImageEditor = false;

      canvas.toBlob((blob) => {
        let config = {
          file: blob,
          name: this.model,
          folder: this.baseFolder,
        };
        this.connector.upload(
          config,
          (data) => {
            this.model = data.url;
          },
          () => {}
        );
      }, this.toMine(this.model));
    },
    remove() {
      this.model = null;
    },
    toMine(filename) {

      if (filename.endsWith(".jpg"))
        return "image/jpeg";
      else if (filename.endsWith(".jpeg"))
        return "image/jpeg";
      else if (filename.endsWith(".png"))
        return "image/png";
      else 
        return "image/png";
    },
  },
  components: { Control, VueCropper, FileBrowser, ImageEditor },
  builder: {
    props() {
      return {
        schema: {
          type: "object",
          properties: {
            required: {
              title: "Required",
              type: "boolean",
            },
            multilanguage: {
              title: "Multi language",
              type: "boolean",
            },
          },
        },
        options: {},
      };
    },
    fromBuilder(field) {
      return {
        schema: {
          title: field.label,
          type: "string",
          required: field.required,
        },
        options: {
          type: "image",
          multilanguage: field.multilanguage,
        },
      };
    },
    toBuilder(def) {
      return {
        label: def.schema.title,
        fieldType: "image",
        required: def.schema.required,
        multilanguage: def.options.multilanguage,
      };
    },
  },
};

export default ImageField;
</script>

